@import url(https://fonts.googleapis.com/css?family=Gothic+A1|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Agenda";
  src: url(/BitKings-App/static/media/agenda-light.05988561.woff) format('woff');
}


.App {
  font-family: 'Roboto', sans-serif;
  font-family: 'Gothic A1', sans-serif;
}

.open {
  display: block;
  height: auto;
  border-bottom: 0.5px solid rgb(199, 197, 197);
  padding: 18px;
}

.close {
  display: none;
}

ol {
  text-align: left;
  margin-bottom: 0.8em;
}

li {
  text-align: left;
}

.p-text {
  text-align: left;
  font-family: 'Gothic A1', sans-serif;
}

.img-small {
  width: 30vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}

.img-medium {
  width: 50vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}

.img-tiny {
  width: 10vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}
