@font-face {
  font-family: "Agenda";
  src: url('./font/agenda-light.woff') format('woff');
}
@import url('https://fonts.googleapis.com/css?family=Gothic+A1|Roboto&display=swap');


.App {
  font-family: 'Roboto', sans-serif;
  font-family: 'Gothic A1', sans-serif;
}

.open {
  display: block;
  height: auto;
  border-bottom: 0.5px solid rgb(199, 197, 197);
  padding: 18px;
}

.close {
  display: none;
}

ol {
  text-align: left;
  margin-bottom: 0.8em;
}

li {
  text-align: left;
}

.p-text {
  text-align: left;
  font-family: 'Gothic A1', sans-serif;
}

.img-small {
  width: 30vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}

.img-medium {
  width: 50vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}

.img-tiny {
  width: 10vw;
  margin: 0 auto;
  display: block;
  padding: 20px 0px 20px 0px;
}